<template>
  <div class="mask" v-if="showCart && orderInfo.count > 0"></div>
  <div class="cart">
    <div class="product" v-if="showCart && orderInfo.count > 0">
      <div class="product__header">
        <div class="product__header__all" @click="() => changeAllChecked(shopId)">
          <span class="product__header__icon iconfont" v-html="orderInfo.isAllChecked ? '&#xe63a;' : '&#xe7ae;'"></span>
          全选
        </div>
        <div class="product__header__clear" @click="() => cleanCartProduct(shopId)">清空购物车</div>
      </div>
      <ul class="product__list">
        <template v-for="item in productList" :key="item._id" >
          <li class="product__item" v-if="item.count > 0">
            <div class="product__item__checked iconfont" @click.stop="() => changeCartItemCheck(shopId, item)" v-html="item.check ? '&#xe63a;' : '&#xe7ae;'"></div>
            <img :src="item.imgUrl" alt="" />
            <div class="product__item__info">
              <h4 class="product__item__title">{{ item.name }}</h4>
              <p class="product__item__price">
                <span class="product__item__yen">&yen;</span>{{item.price}}
                <span class="product__item__originyen">{{item.oldPrice}}</span>
              </p>
            </div>
            <div class="product__number">
              <span class="product__number__desc" @click="() => { changeCartItemInfo(shopId, item, -1) }">-</span>
              <span class="product__number__text">{{item.count || 0}}</span>
              <span class="product__number__insc" @click="() => { changeCartItemInfo(shopId, item, 1) }">+</span>
            </div>
          </li>
        </template>
      </ul>
    </div>
    <div class="check">
      <div class="check__icon" @click="changeShowCart">
        <img
          class="check__icon__img"
          src="http://www.dell-lee.com/imgs/vue3/basket.png"
          alt=""
        />
        <div class="check__icon__tag">{{ orderInfo.count }}</div>
      </div>
      <div class="check__info">
        总计：<span class="check__info__price">&yen; {{ orderInfo.price }}</span>
      </div>
      <div class="check__btn">
        <router-link :to="`/orderConfirm/${shopId}`">
          去结算
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'
import { useRoute } from 'vue-router'
import { useStore } from 'vuex'
import { useCommonCartEffect } from '../../effects/cartEffects'

// 购物车信息相关逻辑
const useCartEffect = shopId => {
  const { changeCartItemInfo, productList, orderInfo } = useCommonCartEffect(shopId)
  const store = useStore()
  // 购物车内商品的选中态
  const changeCartItemCheck = (shopId, product) => {
    store.commit('changeCartItemCheck', {
      shopId, product
    })
  }
  // 清空购物车
  const cleanCartProduct = shopId => {
    store.commit('cleanCartProducts', { shopId })
  }
  //
  const changeAllChecked = shopId => {
    store.commit('changeAllChecked', { shopId })
  }
  return {
    orderInfo,
    productList,
    changeCartItemInfo,
    changeCartItemCheck,
    cleanCartProduct,
    changeAllChecked
  }
}

// 展示隐藏购物车逻辑
const toggleCartEffect = () => {
  const showCart = ref(false)
  const changeShowCart = () => {
    showCart.value = !showCart.value
  }
  return { showCart, changeShowCart }
}

export default {
  name: 'Cart',
  setup () {
    const route = useRoute()
    const shopId = route.params.id
    const {
      orderInfo, productList,
      changeCartItemInfo, changeCartItemCheck, cleanCartProduct, changeAllChecked
    } = useCartEffect(shopId)
    const { showCart, changeShowCart } = toggleCartEffect()
    return {
      shopId,
      orderInfo,
      productList,
      showCart,
      changeCartItemInfo,
      changeCartItemCheck,
      cleanCartProduct,
      changeAllChecked,
      changeShowCart
    }
  }
}
</script>

