<template>
  <div class="content">
    <div class="category">
      <ul class="category__list">
        <li
          v-for="item in categories"
          :key="item.name"
          :class="{
            category__item: true,
            'category__item--active': currentTab === item.tab ? true : false,
          }"
          @click="() => handleTabClick(item.tab)"
        >
          {{ item.name }}
        </li>
      </ul>
    </div>
    <div class="product">
      <ul class="product__list">
        <li v-for="item in list" :key="item._id" class="product__item">
          <img :src="item.imgUrl" alt="" />
          <div class="product__item__info">
            <h4 class="product__item__title">{{ item.name }}</h4>
            <p class="product__item__sales">月售 {{ item.sales }}</p>
            <p class="product__item__price">
              <span class="product__item__yen">&yen;</span>{{item.price}}
              <span class="product__item__originyen">{{item.oldPrice}}</span>
            </p>
          </div>
          <div class="product__number">
            <span class="product__number__desc" @click="() => { changeCartItem(shopId, item, -1, shopName) }">-</span>
            <span class="product__number__text">{{cartList?.[shopId]?.productList?.[item._id]?.count || 0}}</span>
            <span class="product__number__insc" @click="() => { changeCartItem(shopId, item, 1, shopName) }">+</span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { reactive, ref, toRefs, watchEffect } from 'vue'
import { get } from '../../utils/request'
import { useRoute } from 'vue-router'
import { useCommonCartEffect } from '../../effects/cartEffects'
import { useStore } from 'vuex'

const categories = [
  {
    name: '全部商品',
    tab: 'all'
  },
  {
    name: '秒杀',
    tab: 'seckill'
  },
  {
    name: '新鲜水果',
    tab: 'fruit'
  }
]

// tab切换逻辑
const useTabEffect = () => {
  const currentTab = ref(categories[0].tab)
  const handleTabClick = (tab) => {
    currentTab.value = tab
  }
  return { currentTab, handleTabClick }
}

// 列表内容逻辑
const useCurrentListEffect = (currentTab, shopId) => {
  const content = reactive({
    list: []
  })
  const getContentData = async () => {
    const result = await get(`/api/shop/${shopId}/products`, {
      tab: currentTab.value
    })
    if (result.errno === 0 && result.data) {
      content.list = result.data
    }
  }
  // 刚加载或依赖发生变化
  watchEffect(() => {
    getContentData()
  })
  const { list } = toRefs(content)
  return { list }
}

// 购物逻辑
const useCartEffect = () => {
  const store = useStore()
  const { changeCartItemInfo } = useCommonCartEffect()
  const changeShopName = (shopId, shopName) => {
    store.commit('changeShopName', {
      shopId, shopName
    })
  }
  const changeCartItem = (shopId, item, num, shopName) => {
    changeCartItemInfo(shopId, item, num)
    changeShopName(shopId, shopName)
  }
  return { changeShopName, changeCartItem }
}

export default {
  name: 'Content',
  props: ['shopName'],
  setup () {
    const route = useRoute()
    const shopId = route.params.id
    const { currentTab, handleTabClick } = useTabEffect()
    const { list } = useCurrentListEffect(currentTab, shopId)
    const { cartList } = useCommonCartEffect()
    const { changeCartItem } = useCartEffect()
    return { cartList, categories, currentTab, changeCartItem, shopId, list, handleTabClick }
  }
}
</script>

