import { computed } from 'vue'
import { useStore } from 'vuex'

// 购物车相关
const useCommonCartEffect = (shopId) => {
  const store = useStore()
  const { cartList } = store.state
  // 添加购物车
  const changeCartItemInfo = (shopId, product, num) => {
    store.commit('changeCartItemInfo', { shopId, product, num })
  }
  // 商店商品列表
  const productList = computed(() => {
    const shopInfo = cartList[shopId] || {}
    const productList = shopInfo.productList || {}
    console.log(productList)
    return productList
  })
  // 商店名称
  const shopName = computed(() => {
    const shopInfo = cartList[shopId] || {}
    const name = shopInfo.shopName
    return name
  })
  // 订单详情
  const orderInfo = computed(() => {
    let count = 0
    let price = 0
    let isAllChecked = true
    const shopInfo = cartList[shopId]
    if (shopInfo) {
      const productList = shopInfo.productList || {}
      for (const i in productList) {
        count += productList[i].count
        if (productList[i].check) {
          price += productList[i].count * productList[i].price
        }
        if (productList[i].count > 0 && !productList[i].check) {
          isAllChecked = false
        }
      }
    }
    return { count, price: price.toFixed(2), isAllChecked }
  })
  return { cartList, productList, shopName, orderInfo, changeCartItemInfo }
}

export { useCommonCartEffect }
